/**
 * 
 * @param {string} path - 
 * @param {{ strict?: boolean }} options - strict  true  "." true
 * @returns {{ valid: boolean, error?: string }} 
 */
export function validateFolderPath(path, { strict = true } = {}) {
    // 
    if (!path || path.trim() === '') {
        return { valid: true };
    }
    
    //  "/"
    if (path === '/') {
        return { valid: true };
    }
    
    //  "/" 
    if (!path.startsWith('/')) {
        return { 
            valid: false, 
            error: ' "/" ' 
        };
    }

    //  .. 
    if (/(?:^|\/)\.\.(\/|$)/.test(path) || path.includes('..')) {
        return {
            valid: false,
            error: ' ".."'
        };
    }

    //  . 
    // strict  /./  /. strict  /./  /.123
    const dotPattern = strict ? /\/\.(\/|$)/ : /\/\.\//;
    if (dotPattern.test(path)) {
        return {
            valid: false,
            error: ' "." '
        };
    }

    // 
    if (path.includes('//')) {
        return { 
            valid: false, 
            error: '' 
        };
    }
    
    // 
    // : \ : * ? " ' < > |  ( ) [ ] { } # % ^ ` ~ ; @ & = + $ ,
    const invalidChars = /[\\:\*\?"'<>\| \(\)\[\]\{\}#%\^`~;@&=\+\$,]/;
    if (invalidChars.test(path)) {
        return { 
            valid: false, 
            error: '' 
        };
    }
    
    return { valid: true };
}
